
  .form {
    border-radius: 8px;
    padding: 30px;
    margin-bottom: 20px;
    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .label {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 20px;
    padding: 10px;
    width: 380px;
  }
  .inputName {
    margin-right: 15px;
  }
  .btn {
    background-color: #f0f8ff;
    padding: 10px 20px 10px 20px;
    border-radius: 8px;
    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    font-family: 'Ibarra Real Nova', serif;
    font-size: 20px;
    border: none;
    display: flex;
    align-items: center;
    justify-content: center;
    justify-content: space-between;
    margin-top: 20px;
  }
  .btn:active {
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  }
  
  .btn:hover {
    background-color: #191970;
    color: #fff;
  }
  
  .input {
    border: 1px solid #87cefa;
    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    display: block;
    height: 30px;
    width: 250px;
    border-radius: 20px;
    background-color: #f0f8ff;
    outline: none;
    padding-left: 20px;
    padding-right: 20px;
    font-size: 14px;
  }
  .input:focus {
    border-color: #191970;
  }
  .Loader {
    margin-left: 10px;
    width: 20px;
    height: 20px;
  }
  .container {
    margin-left: auto;
    margin-right: auto;
    background-color: #87cefa;
    width: 500px;
    margin-top: 50px;
    margin-bottom: 50px;
    border-radius: 8px;
    box-shadow: 20px -15px 10px 5px rgba(0, 0, 0, 0.5);
    padding: 40px;
    font-family: 'Ibarra Real Nova', serif;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .title{
    font-size: 30px;
    margin-bottom: 25px;
    font-weight: 600;
  }
  