.phonebookContainer {
    margin-left: auto;
    margin-right: auto;
    background-color: #87cefa;
    width: 500px;
    margin-top: 50px;
    margin-bottom: 50px;
    border-radius: 8px;
    box-shadow: 20px -15px 10px 5px rgba(0, 0, 0, 0.5);
    padding: 40px;
    font-family: 'Ibarra Real Nova', serif;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .title {
    font-size: 34px;
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
  }
  .Loader {
    display: block;
    margin-left: auto;
    margin-right: auto;
  }
  